*{
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  color: #F5F5F5;
}

.App{
  width: 317px;
  background-color: #1A1B21;
  margin:auto;
  margin-top: 100px;
  border-radius: 20px;
}

.info{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.info--pic{
  width: 317px;
  height: 317px;
  object-fit: cover;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.info--name{
  margin-top: 21px;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  text-align: center;
}

.info--sub{
  margin-top: 6.2px;
  font-weight: normal;
  font-size: 12.8px;
  line-height: 15px;
  text-align: center;
  color: #F3BF99;
}

.info--website{
  margin-top: 8.8px;
  font-style: normal;
  font-weight: normal;
  font-size: 10.24px;
  line-height: 150%;
  text-align: center;
}

.info--button{
  margin-top: 16px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 9px 13px 9px 11px;
  background: #5093E2;
  border: none;
  width: 247px;
  height: 34px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
}

.info--button>a{
  text-decoration: none;
}

.info--btnimg{
  padding: 9.6px;
}

.about--title, .interests--title{
  font-style: normal;
  margin: 0 35px;
  margin-top: 33px;
  font-weight: bold;
  font-size: 16px;
  line-height: 150%;
  color: #F5F5F5;
}

.interests--title{
  margin-top: 27px;
}

.about--para, .interests--para{
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 10.24px;
  line-height: 150%;
  margin: 0 35px;
  margin-top: 5px;
  color: #DCDCDC;
}

.footer{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 317px;
  height: 64px;
  margin-top: 24px;

  background: #161619;
  border-radius: 0px 0px 10px 10px;
}

.footer-icon{
  margin: 20px 12px;
}